<template>
  <v-row justify="center" class="mt-6 ">
    <v-col cols="12" md="6">
      <v-text-field v-model="title" label="title"></v-text-field>
      <v-text-field v-model="body" label="description"></v-text-field>
      <v-btn @click="submit" color="primary">Create</v-btn>
    </v-col>
  </v-row>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  name: "postForm",
  data() {
    return {
      title: '',
      body: ''
    }
  },
  mounted() {
    if (localStorage.title) {
      this.title = localStorage.title
    }
    if (localStorage.body) {
      this.body = localStorage.body
    }
  },
  watch: {
    title(newTitle) {
      localStorage.title = newTitle;
    },
    body(newBody) {
      localStorage.body = newBody;
    }
  },
  methods: {
    ...mapMutations(['createPost']),
    submit() {
      if (this.title && this.body) {
        this.createPost({
          title: this.title,
          body: this.body,
          editMode: false,
          id: Date.now()
        })
      }
      this.title = this.body = ''
    }
  }
}
</script>

