<template>
  <v-container>
    <v-row>
      <v-col>
        <h1>Hello World</h1>
        <h2 v-if="getPosts.length > 0">You have {{ getPosts.length }} posts</h2>
        <h2 v-else>You have no posts, create now!</h2>
        <post-card />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import PostForm from "@/components/postForm";
import PostCard from "@/components/postCard";

export default {
  name: 'postList',
  components: {PostCard, PostForm},
  computed: {
    ...mapGetters(['getPosts'])
  },
  methods: {
    ...mapActions(['fetchPosts'])
  },
  mounted() {
    this.fetchPosts()
  }
}
</script>
