<template>
  <v-row justify="center">
    <v-col cols="12" md="6">
      <v-card class="ma-4 elevation-10" v-for="post in getPosts" :key="post.id">
        <v-card-title v-if="!post.editMode">{{ post.title }}</v-card-title>
        <v-text-field v-else label="edit title" v-model="post.title"></v-text-field>
        <v-card-text>{{ post.body }}</v-card-text>
        <v-btn @click="deletePost(post.id)" class="ma-2" color="red">Delete</v-btn>
        <v-btn v-if="!post.editMode" @click="editMode(post)" color="green">Edit</v-btn>
        <v-btn v-else @click="savePost(post)" color="blue">Save</v-btn>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  name: "postCard",
  computed: {
    ...mapGetters(["getPosts"]),
  },
  methods: {
    ...mapMutations(["deletePost"]),
    editMode(post) {
      post.editMode = true;
    },
    savePost(post) {
      post.editMode = false;
    },
  },
};
</script>

